import React from 'react';
import renderHTML from 'react-render-html';
import WrapperContent from './WrapperContent';

function Content({ content }) {

  const renderScore = (value) => {
    const result = [];
    const maxValue = 5;
    const dataNull = maxValue - value;
    for (let i = 0; i < value; i++) {
      result.push(<div className='item-score item-fill'></div>)
    }
    for (let i = 0; i < dataNull; i++) {
      result.push(<div className='item-score item-null'></div>)
    }
    return <div className='box-score'>{result}</div>
  }

  const renderContent = (item) => {
    const dataContent = [];
    if (item.title) {
      dataContent.push(
        <b>{item.title}</b>
      )
    }
    if (item.list) {
      dataContent.push(
        <ul className='list-content'>
          {item.list.map(itemList => <li className='text-content'>{renderHTML(itemList)}</li>)}
        </ul>
      )
    }

    if (item.itemScore) {
      dataContent.push(
        <ul className='list-score'>
          {item.itemScore.map(score => (
            <li>
              <div className='text-content title-score'>{score.title}</div>
              {renderScore(score.value)}
            </li>
          ))}
        </ul>
      )
    }

    if (item.text) {
      dataContent.push(<div className='text-content'>{renderHTML(item.text)}</div>)
    }

    return dataContent;
  }

  return (
    <WrapperContent>
      {content && content.map(item => <div className='item-content'>{renderContent(item)}</div>)}
    </WrapperContent>
  )
}

export default Content;
